/* sales-block */
.sales-block{
	font-size: 13px;
	color: #666;
	display: block;
	text-align: center;
	margin-top: 10px;
	line-height: 18px;
	text-decoration: none;
  border-radius: 4px;
  padding: 120px 15px 30px;
  background: #f1f1f1 resolve('sales.png') no-repeat top center;
  @media (max-width: 991px){
  	margin-bottom: 30px;
  }
  @nest &--image{
  	padding-top: 105px;
  	background: #f1f1f1 resolve('right.png') no-repeat center 20px;
  }
  @nest &--circ{
    margin: 0 0 15px;
    text-align: left;
    padding: 120px 15px 25px 30px;
    background: #f1f1f1 resolve('circ.png') no-repeat center 12px;
  }
  @nest &__link{
    font-size: 13px;
    color: #666;
    display: block;
    margin-bottom: 20px;
    @nest &:hover{
      text-decoration: none;
    }
  }
}