/* modal */
.modal{
	display: none;
	background: transparent;
	@nest & img{
		@media (max-width: 991px){
			display: block;
			margin: 0 auto 20px;
		}
	}
	@nest &__title{
		font-size: 60px;
		color: #fff;
		font-weight: 100;
		margin: 0 0 30px;
		@media (max-width: 767px){
			font-size: 40px;
		}
		@media (max-width: 512px){
			font-size: 27px;
		}
	}
	@nest &__adress{
		font-size: 15px;
		color: #fff;
		display: block;
	}
	@nest &__tel{
		font-size: 36px;
		color: #fff;
		display: block;
		margin: 35px 0 0;
		font-weight: 700;
	}
	@nest &__about{
		padding-right: 75px;
		max-height: 540px;
		overflow-x: auto;
		@nest & p{
			font-size: 15px;
			color: #fff;
			margin: 20px 0;
			line-height: 26px;
		}
	}
}
.fancybox-skin{
	padding: 0 !important;
	box-shadow: none !important;
	background: transparent !important;
}
.fancybox-close{
	right: 0;
	top: 0;
	background: resolve('close.png');
	width: width('close.png');
	height: height('close.png');
}
.fancybox-inner{
	height: auto !important;
}
.fancybox-overlay{
	z-index: 100;
	background: color(#323232 alpha(90%));
}
#ascrail2000{
	z-index: 999 !important;
	opacity: 1 !important;
	border-radius: 0 !important;
	background: #212121 !important;
}
.nicescroll-cursors{
	cursor: pointer;
	z-index: 999 !important;
	width: 8px !important;
	border: 0 !important;
	background: #21a0e4 !important;
	border-radius: 0 !important;
}