/* circular-slider */
.circular-slider{
	margin-bottom: 25px;
	@nest &__item{
		outline: none;
		@nest & img{
			width: 100%;
		}
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 50%;
		font-size: 0;
		border: 0;
		z-index: 10;
		outline: none;
		border-radius: 50%;
		transition: opacity 300ms;
		transform: translateY(-50%);
		@nest &:hover{
			opacity: .7;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			background: resolve('after.png');
			width: width('after.png');
			height: height('after.png');
		}
	}
	@nest & .slick-prev{
		width: 45px;
		height: 45px;
		left: 10px;
		background: #fff;
		@nest &:after{
			transform: translate(-50%, -50%) rotate(-180deg);
		}
	}
	@nest & .slick-next{
		width: 45px;
		height: 45px;
		right: 10px;
		background: #fff;
	}
	@nest & .slick-disabled{
		background: transparent;
		border: 1px solid #fff;
	}
}