/* circular-list */
.circular-list{
	padding: 0;
	margin: 40px 0;
	list-style: none;
	@nest &__title{
		font-size: 14px;
		color: #2d2d2d;
		font-weight: 700;
		font-family: 'Arial';
	}
	@nest &__item{
		font-size: 14px;
		color: #2d2d2d;
		margin: 7px 0;
		padding-left: 18px;
		position: relative;
		font-family: 'Arial';
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	@nest &__link{
		color: var(--blue);
		@nest &:hover{
			text-decoration: none;
		}
	}
}