/* cruises-block */
.cruises-block{
	max-width: 300px;
	background: #fff;
	display: inline-block;
	vertical-align: top;
	margin: 0 10px 20px;
	@media (max-width: 767px){
		max-width: 100%;
	}
	@nest &__image{
		width: 100%;
		height: 167px;
	}
	@nest &__content{
		text-align: center;
		padding: 20px 5px 10px;
		box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.15);
	}
	@nest &__title{
		font-size: 16px;
		font-weight: 700;
		color: var(--blue);
		margin: 0 0 10px;
	}
	@nest &__desc{
		font-size: 12px;
		color: #8c8c8c;
		display: block;
		margin: 0 0 10px;
		@nest &--size{
			font-size: 18px;
		}
	}
	@nest &__date{
		display: block;
		color: #3d3938;
		font-size: 12px;
		margin: 0 0 25px;
		@media (max-width: 512px){
			margin-bottom: 15px;
		}
		@nest &--size{
			font-size: 14px;
			margin-bottom: 15px;
		}
		@nest &-arrow{
			margin: 0 15px;
			vertical-align: middle;
			display: inline-block !important;
			@media (max-width: 512px){
				margin: 0 7px;
			}
		}
	}
	@nest &__price-wrap{
		margin-top: -10px;
		display: inline-block;
		vertical-align: top;
	}
	@nest &__price{
		font-size: 30px;
		color: var(--blue);
		font-family: var(--font);
		@media (max-width: 512px){
			font-size: 20px;
		}
		@nest &--indent{
			margin-top: -5px;
			display: block;
		}
	}
	@nest &__price-sm{
		font-size: 12px;
		color: #859ba9;
		display: block;
		margin-bottom: 15px;
		font-family: var(--font);
		@nest &--old{
			text-decoration: line-through;
		}
		@nest &--indent{
			margin-left: 5px;
			margin-right: 5px;
			display: inline-block;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		outline: none;
		min-height: 45px;
		padding: 0 20px;
		background: var(--blue);
		transition: background 300ms;
		@nest &:hover{
			background: #04598f;
		}
		@nest &--right{
			float: right;
			margin-top: 5px;
			@media (max-width: 512px){
				float: none;
				display: block;
				margin-top: 0;
			}
		}
		@nest &--indent-top{
			float: right;
			margin-top: 5px;
			@media (max-width: 991px){
				float: none;
				display: block;
				margin-top: 0;
			}
		}
	}
	@nest &__price-old{
		font-size: 30px;
		color: #d6d6d6;
		margin: 0 10px;
		display: inline-block;
		vertical-align: middle;
		text-decoration: line-through;
	}
	@nest &__price-new{
		font-size: 30px;
		color: #f70000;
		margin: 0 10px;
		display: inline-block;
		vertical-align: middle;
	}
}