/* recommended-block */
.recommended-block{
	margin: 25px 0 0;
	@nest &__title{
		display: block;
		font-size: 14px;
		color: #666;
		margin-bottom: 5px;
	}
	@nest &__link{
		font-size: 12px;
		color: #a0b5c1;
		display: block;
		text-decoration: none;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}