/* block-title */
.block-title{
	font-size: 0;
	@nest &__left{
		width: 50%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			width: 100%;
		}
	}
	@nest &__right{
		width: 50%;
		text-align: right;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			width: 100%;
			text-align: left;
		}
	}
	@nest &__caption{
		font-size: 48px;
		font-weight: 100;
		color: #fff;
		@nest &--black{
			color: #000;
		}
		@nest &--gray{
			color: #303030;
		}
		@media (max-width: 991px){
			font-size: 35px;
		}
		@media (max-width: 767px){
			font-size: 30px;
		}
	}
	@nest &__desc{
		font-size: 18px;
		color: #fff;
		@media (max-width: 991px){
			font-size: 16px;
		}
	}
	@nest &__link{
		font-size: 16px;
		color: var(--blue);
		@nest &:hover{
			text-decoration: none;
		}
	}
}