/* circular-table */
.wrap-circular-table{
	@media (max-width: 512px){
		overflow-x: auto;
	}
}
.circular-table{
	font-size: 0;
	min-width: 460px;
	@nest &__row{
		padding: 11px 0 6px;
		border-top: 1px solid #e4e4e4;
		@nest &:last-child{
			border-bottom: 1px solid #e4e4e4;
		}
		@nest &--bg{
			font-weight: 700;
			background: #e5eef3;
		}
	}
	@nest &__col{
		font-size: 12px;
		padding-left: 7px;
		display: inline-block;
		vertical-align: top;
		@nest &--title{
			font-size: 14px;
		}
		@nest &--sm{
			width: 10%;
		}
		@nest &--md{
			width: 20%;
		}
		@nest &--lg{
			width: 25%;
		}
		@nest &--xl{
			width: 45%;
		}
	}
}