/* reviews-slider */
.reviews-slider{
	margin: 25px 0 0;
	@nest &__item{
		outline: none;
	}
	@nest &__slide{
		font-size: 0;
		background: #fff;
		position: relative;
		margin-bottom: 35px;
		padding: 30px 35px 35px 45px;
		box-shadow: 0px 1px 18px 0px rgba(0, 0, 0, 0.11);
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 100%;
			background: resolve('rev.png');
			width: width('rev.png');
			height: height('rev.png');
		}
	}
	@nest &__image{
		width: 117px;
		margin: 5px 0 0;
		vertical-align: top;
		display: inline-block !important;
	}
	@nest &__text{
		width: 78%;
		padding-left: 25px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 767px){
			width: 100%;
			padding-left: 0;
			margin: 20px 0 0;
		}
	}
	@nest &__link{
		font-size: 22px;
		color: var(--blue);
		display: block;
		margin-bottom: 15px;
		@nest &:hover{
			text-decoration: none;
		}
		@media (max-width: 767px){
			font-size: 18px;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #404040;
		display: block;
		line-height: 18px;
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 50%;
		border: 0;
		font-size: 0;
		z-index: 99;
		outline: none;
		transform: translateY(-50%);
	}
	@nest & .slick-prev{
		left: -90px;
		background: resolve('left3.png');
		width: width('left3.png');
		height: height('left3.png');
		@media (max-width: 1199px){
			left: -30px;
		}
		@media (max-width: 767px){
			left: 30%;
			top: auto;
			bottom: -50px;
			transform: translateY(0);
		}
		@nest &.slick-disabled{
			background: resolve('left2.png');
			width: width('left2.png');
			height: height('left2.png');
		}
	}
	@nest & .slick-next{
		right: -90px;
		background: resolve('right2.png');
		width: width('right2.png');
		height: height('right2.png');
		@media (max-width: 1199px){
			right: -30px;
		}
		@media (max-width: 767px){
			right: 30%;
			top: auto;
			bottom: -50px;
			transform: translateY(0);
		}
		@nest &.slick-disabled{
			background: resolve('right3.png');
			width: width('right3.png');
			height: height('right3.png');
		}
	}
}