/* opinions-news */
.opinions-news{
	margin-top: 10px;
	border: 1px solid #d4d4d4;
	@nest & .reviews-slider__slide{
		box-shadow: none;
		margin-bottom: 0;
		@nest &:after{
			display: none;
		}
	}
	@nest & .reviews-slider__text{
		@nest &--lg{
			width: 100%;
			padding-left: 0;
			margin-top: 0;
		}
	}
	@nest &__more{
		font-size: 14px;
		color: var(--blue);
		text-decoration: none;
		position: relative;
		@nest &:hover{
			text-decoration: underline;
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: -12px;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('more.png');
			width: width('more.png');
			height: height('more.png');
		}
	}
}