/* modal-form */
.modal-form{
	@nest &__inp{
		border: 0;
		width: 100%;
		outline: none;
		font-size: 18px;
		color: #949494;
		min-height: 50px;
		background: #fff;
		padding-left: 20px;
		margin-bottom: 10px;
		@nest &::placeholder{
			font-size: 18px;
			color: #949494;
		}
		@nest &--area{
			padding-top: 17px;
			min-height: 200px;
			resize: none;
		}
	}
	@nest &__btn{
		font-size: 15px;
		color: #fff;
		border: 0;
		outline: none;
		width: 100%;
		display: block;
		line-height: 50px;
		text-decoration: none;
		text-align: center;
		min-height: 50px;
		background: #209ee4;
		transition: background 300ms;
		@nest &:hover{
			background: #1674a9;
		}
		@nest &--indent{
			margin-top: 15px;
		}
	}
}