/* interesting-block */
.interesting-block{
	height: 405px;
	display: block;
	position: relative;
	text-decoration: none;
	transition: transform 300ms;
	@media (max-width: 512px){
		height: 195px;
		width: 220px;
		margin: auto;
	}
	@nest &:after{
		position: absolute;
		content: '';
		left: 0;
		bottom: 0;
		width: 100%;
		background: resolve('in1.png');
		height: height('in1.png');
	}
	@nest &:hover{
		transform: scale(.99);
	}
	@nest &--sm{
		height: 195px;
		margin-bottom: 15px;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			bottom: 0;
			width: 100%;
			background: resolve('in2.png');
			height: height('in2.png');
		}
	}
	@nest &__text{
		position: absolute;
		content: '';
		left: 30px;
		bottom: 30px;
		z-index: 10;
		font-weight: 300;
		font-size: 36px;
		color: var(--blue);
		@media (max-width: 512px){
			font-size: 18px;
			left: 15px;
			bottom: 15px;
		}
		@nest &--sm{
			font-size: 18px;
			left: 15px;
			bottom: 15px;
		}
	}
}