/* trip-block */
.trip-block{
	padding: 15px;
	margin: 10px 0;
	font-size: 0;
	border: 1px solid #d4d4d4;
	@nest &__left{
		width: 45%;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 767px){
			width: 100%;
			display: block;
		}
	}
	@nest &__right{
		width: 55%;
		display: inline-block;
		vertical-align: top;
		padding-left: 20px;
		@media (max-width: 767px){
			width: 100%;
			display: block;
			padding-left: 0;
			margin-top: 20px;
		}
	}
	@nest &__btn{
		font-size: 12px;
		color: #859ba9;
		font-family: 'Arial';
		text-align: right;
		margin: -5px 0 0;
		cursor: pointer;
		@nest &:hover{
			text-decoration: underline;
		}
		@media (max-width: 512px){
			display: block;
			margin-top: 10px;
			text-align: left;
		}
	}
}