/* cruises-slider */
.cruises-slider{
	margin: 25px 0 0;
	font-size: 0;
	@media (max-width: 1199px){
		text-align: center;
	}
	@nest &__item{
		outline: none;
		@nest &--hide{
			@media (max-width: 767px){
				display: none;
			}
		}
	}
	@nest &__wrap{
		margin: 0 -10px;
		@media (max-width: 1199px){
			margin: 0;
		}
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 50%;
		border: 0;
		outline: none;
		transform: translateY(-50%);
	}
	@nest & .slick-prev{
		left: -90px;
		background: resolve('left1.png');
		width: width('left1.png');
		height: height('left1.png');
		@media (max-width: 1199px){
			left: -30px;
		}
		@media (max-width: 767px){
			left: 30%;
			top: auto;
			bottom: -50px;
			transform: translateY(0);
		}
	}
	@nest & .slick-next{
		right: -90px;
		background: resolve('right1.png');
		width: width('right1.png');
		height: height('right1.png');
		@media (max-width: 1199px){
			right: -30px;
		}
		@media (max-width: 767px){
			right: 30%;
			top: auto;
			bottom: -50px;
			transform: translateY(0);
		}
	}
}