/* pages */
.pages{
	clear: fix;
	text-align: center;
	margin: 25px 0 90px;
	@media (max-width: 991px){
		margin-bottom: 25px;
	}
	@nest &__prev{
		float: left;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@nest & img{
			@media (max-width: 512px){
				max-width: 70%;
			}
		}
	}
	@nest &__next{
		float: right;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@nest & img{
			@media (max-width: 512px){
				max-width: 70%;
			}
		}
	}
	@nest &__all{
		margin: 10px 0 0;
		display: inline-block;
		@media (max-width: 512px){
			margin-top: 4px;
		}
	}
	@nest &__single{
		font-size: 16px;
		color: #303030;
		margin: 0 10px;
		position: relative;
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		transition: color 300ms;
		@media (max-width: 512px){
			margin: 0 5px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			width: 25px;
			height: 25px;
			top: 50%;
			left: 50%;
			z-index: -1;
			opacity: 0;
			border-radius: 50%;
			background: var(--blue);
			transition: opacity 300ms;
			transform: translate(-50%, -50%);
		}
		@nest &:hover{
			color: #fff;
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &--active{
			color: #fff;
			@nest &:after{
				opacity: 1;
			}
		}
	}
}