/* nav-list */
.nav-list{
	margin: 0;
	padding: 24px 0 0;
	position: relative;
	list-style: none;
	@nest &__item{
		margin: 0 10px;
		display: inline-block;
		@nest &:first-child{
			margin-left: 0;
		}
		@nest &:last-child{
			margin-right: 0;
		}
		@media (max-width: 1199px){
			margin: 0 5px;
		}
		@media (max-width: 991px){
			margin: 0 0 7px;
			display: block;
			text-align: center;
		}
	}
	@nest &__link{
		font-size: 14px;
		color: #fff;
		text-decoration: none;
		transition: color 300ms;
		@nest &:hover{
			color: #4db8f5;
		}
		@media (max-width: 767px){
			font-size: 17px;
		}
	}
}