/* header */
.header{
	position: absolute;
	content: '';
	top: 0;
	left: 0;
	right: 0;
	@nest &--bg{
		background: #046;
		position: static;
		@nest & .header__inner{
			padding: 0 0 25px;
			min-height: 0;
			@nest &:before{
				display: none;
			}
			@nest &:after{
				display: none;
			}
		}
	}
	@nest &__inner{
		min-height: 95px;
		position: relative;
		@nest &:before{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 25px;
			height: 1px;
			z-index: 5;
			background: #948a82;
			@media (max-width: 991px){
				display: none;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 26px;
			height: 1px;
			z-index: 5;
			background: #3b342f;
			@media (max-width: 991px){
				display: none;
			}
		}
	}
	@nest &__tel{
		font-size: 17px;
		color: #fff;
		font-weight: 700;
		position: relative;
		padding: 0 0 0 27px;
		display: inline-block;
		vertical-align: middle;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('tel.png');
			width: width('tel.png');
			height: height('tel.png');
		}
		@media (max-width: 991px){
			margin-right: 5px;
		}
		@media (max-width: 512px){
			margin: 95px 0 0;
		}
	}
	@nest &__left{
		padding: 23px 0 0;
		position: relative;
		@media (max-width: 991px){
			padding: 0;
			text-align: center;
		}
	}
	@nest &__logo{
		position: absolute;
		content: '';
		right: 0;
		top: 0;
		z-index: 10;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 991px){
			position: static;
			display: inline-block;
			vertical-align: middle;
		}
		@media (max-width: 512px){
			position: absolute;
			right: auto;
			left: 50%;
			transform: translateX(-50%);
		}
	}
}