@import "normalize-css/normalize.css";
@import "slick-carousel/slick/slick.css";
@import "fancybox/source/jquery.fancybox.css";
/*! main */
@font-face {
    font-family: 'qanelas';
    src: url('../fonts/qanelas-regular.woff2') format('woff2'),
         url('../fonts/qanelas-regular.woff') format('woff'),
         url('../fonts/qanelas-regular.ttf') format('truetype'),
         url('../fonts/qanelas-regular.svg#qanelasregular') format('svg');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'helvetica';
    src: url('../fonts/helveticaneuecyr-bold.woff2') format('woff2'),
         url('../fonts/helveticaneuecyr-bold.woff') format('woff'),
         url('../fonts/helveticaneuecyr-bold.ttf') format('truetype'),
         url('../fonts/helveticaneuecyr-bold.svg#helveticaneuecyrbold') format('svg');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'helvetica';
    src: url('../fonts/helveticaneuecyr-thin.woff2') format('woff2'),
         url('../fonts/helveticaneuecyr-thin.woff') format('woff'),
         url('../fonts/helveticaneuecyr-thin.ttf') format('truetype'),
         url('../fonts/helveticaneuecyr-thin.svg#helveticaneuecyrthin') format('svg');
    font-weight: 100;
    font-style: normal;
}
@font-face {
    font-family: 'helvetica';
    src: url('../fonts/helveticaneuecyr-roman_1.woff2') format('woff2'),
         url('../fonts/helveticaneuecyr-roman_1.woff') format('woff'),
         url('../fonts/helveticaneuecyr-roman_1.ttf') format('truetype'),
         url('../fonts/helveticaneuecyr-roman_1.svg#helveticaneuecyrroman') format('svg');
    font-weight: 400;
    font-style: normal;
}

html, body{
	font-family: 'helvetica';
	font-weight: 400;
}

:root{
	--font: 'qanelas';
	--blue: #0072ba;
}
.col-indent{
    padding: 0 5px !important;
    @media (max-width: 767px){
        padding: 0 16px !important;
    }
}