/* block-links */
.block-links{
	background: #f9f9f9;
	padding: 30px 10px 60px 30px;
	@media (max-width: 991px){
		margin-bottom: 30px;
	}
	@nest &__item{
		font-size: 14px;
		color: var(--blue);
		display: block;
		margin-bottom: 15px;
		@nest &:hover{
			text-decoration: none;
		}
	}
}