/* footer-list */
.footer-list{
	padding: 0;
	margin: 0;
	list-style: none;
	@media (max-width: 991px){
		margin: 30px 0;
	}
	@nest &__item{
		margin-bottom: 15px;
	}
	@nest &__link{
		font-size: 13px;
		color: #fff;
		text-decoration: none;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}