/* trip-table */
.trip-table-wrap{
	display: none;
	@nest &--show{
		display: block;
	}
	@media (max-width: 512px){
		overflow-x: auto;
	}
}
.trip-table{
	width: 100%;
	margin: 20px 0 0;
	min-width: 400px;
	@nest &__row{
		border-top: 1px solid #e4e4e4;
		@nest &:last-child{
			border-bottom: 1px solid #e4e4e4;
		}
	}
	@nest &__col{
		font-size: 12px;
		color: #859ba9;
		font-weight: 700;
		font-family: 'Arial';
		display: table-cell;
		width: 50%;
		padding: 13px 0 9px;
	}
}