/* interesting-wrap */
.interesting-wrap{
	font-size: 0;
	margin: 30px 0 0;
	@media (max-width: 1199px){
		text-align: center;
	}
	@nest &__block{
		width: 100%;
		max-width: 460px;
		display: inline-block;
		vertical-align: top;
		@nest &--indent-right{
			margin-right: 20px;
			@media (max-width: 1199px){
				margin-right: 0;
			}
		}
	}
	@nest &__col{
		width: 100%;
		display: inline-block;
		vertical-align: top;
		@nest &--sm{
			width: 220px;
		}
		@nest &--indent-right{
			margin-right: 20px;
			@media (max-width: 512px){
				margin-right: 0;
			}
		}
		@media (max-width: 1199px){
			margin-bottom: 15px;
		}
		@media (max-width: 512px){
			display: block;
			margin-left: auto;
			margin-right: auto;
		}
	}
}