/* sales-btn */
.sales-btn{
	font-size: 18px;
	color: #fff;
	border: 0;
	width: 100%;
	min-height: 45px;
	outline: none;
	border-radius: 4px;
	max-width: 230px;
	margin-top: 10px;
	background: var(--blue);
	transition: background 300ms;
	@nest &:hover{
		background: #265dc0;
	}
	@media (max-width: 991px){
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-top: 0;
		margin-bottom: 30px;
	}
}