/* form-selection */
.form-selection{
	font-size: 0;
	@media (max-width: 991px){
		width: 100%;
		max-width: 500px;
		margin: auto;
	}
	@nest &__title{
		font-size: 72px;
		color: #fff;
		font-weight: 100;
		line-height: 61px;
		margin: 0 0 40px;
		@nest &--blue{
			color: var(--blue);
		}
		@nest &--size{
			margin: 0;
			font-size: 48px;
			line-height: 43px;
		}
		@media (max-width: 991px){
			font-size: 50px;
			line-height: normal;
		}
		@media (max-width: 767px){
			font-size: 35px;
		}
	}
	@nest &__block{
		margin-right: 10px;
		display: inline-block;
		vertical-align: top;
		@nest &--sm{
			width: 130px;
		}
		@nest &--md{
			width: 140px;
		}
		@nest &--lg{
			width: 255px;
		}
		@media (max-width: 1199px){
			max-width: 24%;
		}
		@media (max-width: 991px){
			max-width: 100%;
			width: 100%;
			margin-bottom: 15px;
		}
		@nest &-title{
			font-size: 16px;
			color: #fff;
			font-weight: 700;
			margin: 0 0 5px;
			display: block;
		}
	}
	@nest &__input{
		width: 100%;
		border: 0;
		outline: none;
		cursor: pointer;
		min-height: 40px;
		font-size: 15px;
		color: #3d3938;
		padding: 10px 0 10px 17px;
		border-radius: 5px;
		background: #fff;
		appearance: none;
		@nest &::placeholder{
			font-size: 15px;
			color: #3d3938;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		width: 95px;
		outline: none;
		min-height: 40px;
		border-radius: 5px;
		background: #ff9600;
		display: inline-block;
		vertical-align: bottom;
		transition: background 300ms;
		box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.5);
		@nest &:hover{
			background: #d07f0b;
		}
	}
}