/* select */
.select{
	position: relative;
	@nest &__current{
		display: block;
		cursor: pointer;
		min-height: 50px;
		line-height: 50px;
		@nest &--size{
			min-height: 40px;
			line-height: 40px;
		}
	}
	@nest &__list{
		position: absolute;
		content: '';
		top: 100%;
		left: 0;
		right: 0;
		width: 100%;
		z-index: 99;
		display: none;
		margin: 0;
		list-style: none;
		background: #fff;
		padding: 0 0 15px 20px;
		@nest &--indent{
			padding-top: 15px;
			padding-left: 17px;
		}
	}
	@nest &__item{
		cursor: pointer;
		margin-bottom: 5px;
		@nest &:hover{
			text-decoration: underline;
		}
	}
}