/* trip-links */
.trip-links{
	font-size: 0;
	padding-bottom: 70px;
	@media (max-width: 991px){
		padding-bottom: 30px;
	}
	@nest &__item{
		height: 280px;
		width: 345px;
		position: relative;
		margin-bottom: 10px;
		display: inline-block;
		vertical-align: top;
		transition: box-shadow 300ms;
		box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.15);
		@nest &:hover{
			box-shadow: none;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			background: resolve('top2.png') no-repeat;
			height: height('top2.png');
		}
		@nest &--indent-right{
			margin-right: 5px;
		}
		@media (max-width: 1199px){
			width: 333px;
		}
		@media (max-width: 767px){
			display: block;
			margin-left: auto;
			margin-right: auto;
		}
		@media (max-width: 512px){
			width: 250px;
			height: 250px;
		}
		@nest &--lg{
			display: block;
			width: 100%;
			@nest &:after{
				position: absolute;
				content: '';
				left: 0;
				bottom: 0;
				right: 0;
				width: 100%;
				background: resolve('top1.png') no-repeat;
				height: height('top1.png');
			}
			@media (max-width: 512px){
				width: 250px;
				height: 250px;
			}
		}
	}
	@nest &__text{
		position: absolute;
		content: '';
		left: 30px;
		z-index: 10;
		bottom: 30px;
		font-size: 24px;
		color: #3c4347;
		@nest &--sm{
			font-size: 18px;
		}
		@media (max-width: 512px){
			font-size: 16px;
			left: 15px;
			bottom: 15px;
			padding-right: 15px;
		}
	}
}