/* subscribe */
.subscribe{
	padding-left: 50px;
	@media (max-width: 991px){
		padding-left: 0;
		margin: 30px 0;
	}
	@nest &__title{
		font-size: 12px;
		color: #fff;
		display: block;
		margin-bottom: 15px;
		text-transform: uppercase;
	}
	@nest &__inp{
		font-size: 14px;
		color: #fff;
		min-height: 30px;
		width: 100%;
		border: 0;
		outline: none;
		background: #7d8e98;
		padding-left: 10px;
		margin-bottom: 10px;
		@nest &::placeholder{
			font-size: 14px;
			color: #fff;
		}
	}
	@nest &__desc{
		font-size: 12px;
		color: #fff;
		display: block;
	}
	@nest &__social{
		margin-right: 20px;
		display: inline-block;
		vertical-align: middle;
		transition: opacity 300ms;
		@nest &:last-child{
			margin-right: 0;
		}
		@nest &:hover{
			opacity: .7;
		}
	}
}