/* circular-content */
.circular-content{
	margin: 13px 0 0;
	padding: 15px 0 0;
	border-top: 6px solid #1f9ce3;
	@nest &__text{
		font-size: 14px;
		color: #2d2d2d;
		margin: 20px 0 0;
		font-family: 'Arial';
	}
	@nest &__title{
		font-size: 48px;
		font-weight: 100;
		color: var(--blue);
		margin: 40px 0 0;
		padding-bottom: 10px;
		@media (max-width: 767px){
			font-size: 35px;
		}
		@nest &--border{
			border-bottom: 2px solid #e5eef3;
		}
	}
}